<template>
  <div class="d-flex">
    <v-card class="calendar ma-xs-0 ma-sm-5 elevation-4 pa-5 flex-grow-1 flex-shrink-10"
    min-width="0">
      <v-row class="fill-height">
        <v-col>

          <v-sheet class="d-flex ma-4 mt-0">

            <div class="d-flex flex-wrap flex-md-nowrap">

              <v-toolbar-title v-if="$refs.calendar" class="mt-1 mr-3">
                {{ $refs.calendar.title }}
              </v-toolbar-title>

              <div> 
                <v-btn
                  fab
                  text
                  small
                  color="grey darken-2"
                  @click="prev"
                >
                  <v-icon>
                    mdi-chevron-left
                  </v-icon>
                </v-btn>
                <v-btn
                  fab
                  text
                  small
                  color="grey darken-2"
                  @click="next"
                >
                  <v-icon>
                    mdi-chevron-right
                  </v-icon>
                </v-btn>
              </div>

            </div>

            <v-spacer></v-spacer>

            <div class="d-flex flex-wrap flex-md-nowrap justify-end">

              <v-menu
                bottom
                right
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    class="mb-2"
                    outlined
                    color="grey darken-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    <span>{{ typeToLabel[type] }}</span>
                    <v-icon right>
                      mdi-menu-down
                    </v-icon>
                  </v-btn>
                </template>

                <v-list>
                  <v-list-item @click="type = 'day'">
                    <v-list-item-title>Day</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = 'week'">
                    <v-list-item-title>Week</v-list-item-title>
                  </v-list-item>
                  <v-list-item @click="type = 'month'">
                    <v-list-item-title>Month</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>

              <v-btn
                class="ml-4"
                outlined
                color="grey darken-2"
                @click="setToday"
              >
                Aujourd'hui
              </v-btn>

            </div>

          </v-sheet>

          <v-sheet height="600">
            <v-calendar
              ref="calendar"
              v-model="focus"
              color="primary"
              :events="events"
              :event-color="getEventColor"
              :type="type"
              @click:event="showEvent"
              @click:more="viewDay"
              @click:date="viewDay"
            ></v-calendar>
            <v-menu
              v-model="selectedOpen"
              :close-on-content-click="false"
              :activator="selectedElement"
              offset-x
            >
              <v-card
                color="grey lighten-4"
                min-width="350px"
                max-width="400px"
                flat
              >
                <v-toolbar
                  :color="selectedEvent.color"
                  dark
                >
                  <v-btn icon>
                    <v-icon @click="openEdit">mdi-pencil</v-icon>
                  </v-btn>
                  <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                  <v-spacer></v-spacer>
                  <v-btn icon>
                    <v-icon>mdi-dots-vertical</v-icon>
                  </v-btn>
                </v-toolbar>
                <v-card-text>
                  <span v-html="selectedEvent.description"></span>
                </v-card-text>
                <v-card-actions>
                  <v-btn
                    text
                    color="secondary"
                    @click="selectedOpen = false"
                  >
                    Fermer
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-menu>
          </v-sheet>
        </v-col>
      </v-row>

      <v-dialog v-model="editing" width="500">
        <EditProject
          :showEvents="false"
          :propProject="editionProject"
          @closeEdit="closeEdit"
        />
      </v-dialog>

    </v-card>
    <v-card class="d-md-flex hidden-sm-and-down ma-xs-0 ma-sm-5 projects pa-8 elevation-3 flex-shrink-0 justify-center align-center"
    width="350px"
    height="600px">
    <span style="font-style: italic">-- On verra --</span>
    </v-card>
  </div>
</template>

<script>

import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import EditProject from "@/components/project/EditProject.vue";

export default {
  components:{
    EditProject
  },
  data: () => ({
    editing: false,
    editionProject: {},
    focus: '',
    type: 'month',
    typeToLabel: {
      month: 'Mois',
      week: 'Semaine',
      day: 'Jour'
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    oldEvents: [],
    colors: ['blue', 'indigo', 'deep-purple', 'cyan', 'green', 'orange', 'grey darken-1'],
    names: ['Meeting', 'Holiday', 'PTO', 'Travel', 'Event', 'Birthday', 'Conference', 'Party'],
  }),
  created () {
    this.getEvents()
  },
  computed: {
    ...mapGetters("event", ["loading_events", "events"]),
  },
  methods: {
    ...mapActions("event", ["getEvents"]),
    openEdit() {
      this.editionProject = this.selectedEvent.child;
      this.editing = true;
    },
    closeEdit() {
      this.editing = false;
      this.editionProject = {};
      this.getEvents()
    },
    viewDay ({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor (event) {
      return event.color
    },
    setToday () {
      this.focus = ''
    },
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    showEvent ({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        setTimeout(() => {
          this.selectedOpen = true
        }, 10)
      }

      if (this.selectedOpen) {
        this.selectedOpen = false
        setTimeout(open, 10)
      } else {
        open()
      }

      nativeEvent.stopPropagation()
    }
  },
}
</script>

<style scoped>

</style>
